import React from 'react';
import s from '../Lists.module.scss';

export const ButtonsGroup = React.memo(({setRemoveDescription, numberArr, desc, changeItemOrder,id,index}) => {


	return (
		<>
			<button className={s.up} disabled={index === desc.length - 1}
							onClick={() => changeItemOrder(index, numberArr)}>↕
			</button>
			<button className={s.add}>
				Add Sublist
			</button>
			<button className={s.remove}
							onClick={() => setRemoveDescription({id, numberArr})}>
				Remove
			</button>
		</>
	);
});

